import { Card, LinearProgress, Typography } from '@mui/material';
import React from 'react';
import ServerList from './ServerList';

const ServersCard = ({ servers, isLoading }) => {

    return (
        <Card variant="outlined" className='card'>
            <Typography variant="h5" gutterBottom component="h2" className='card__title'>
                Сервера
            </Typography>
            {isLoading ? <LinearProgress color="inherit" /> : <ServerList servers={servers} />}
        </Card>
    )
}

export default ServersCard;