import { ExpandMore } from '@mui/icons-material';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Tooltip, Typography } from '@mui/material';
import React from 'react';
import { getGlobalStatus } from '../utils';

const ServerList = ({ servers }) => {
    return (
        <>
            {servers.map(server => (
                <Accordion key={server.ipAddress}>
                    <AccordionSummary
                        expandIcon={<ExpandMore />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography><span className={`point point--${getGlobalStatus(server)}`} /> {server.ipAddress}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Table>
                            <TableBody>
                                <TableRow>
                                    <TableCell align="left">Доступность</TableCell>
                                    <TableCell align="left"><span className={`point point--${server.isApiAvailable ? 'Ok' : 'Unavailable'}`} /></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell align="left">Redis статус</TableCell>
                                    <TableCell align="left">
                                        <span className={`status status--${server.redisStatus}`} >{server.redisStatus}</span>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell align="left">Память:<br />Полный размер <br />Размер свободной</TableCell>
                                    <TableCell align="left"><br />{server.ram ? `${server.ram.totalSize} Gb` : 'нет данных'} <br />{server.ram ? `${server.ram.free} Gb` : 'нет данных'}</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell align="left">Swap:<br />Полный размер<br /> Размер свободной</TableCell>
                                    <TableCell align="left"><br />{server.ram ? `${server.ram.swapTotalSize} Gb` : 'нет данных'}<br />{server.ram ? `${server.ram.swappFree} Gb` : 'нет данных'}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </AccordionDetails>
                </Accordion>
            ))}
        </>
    )
}

export default ServerList;