import { Delete } from '@mui/icons-material';
import { Button, Card, FormControl, Icon, IconButton, List, ListItem, ListItemText, TextField, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { updateConfig } from '../http/serverAPI';

const SettingsCard = ({ servers }) => {
    const [nodeCount, setNodeCount] = useState();
    const [IP, setIP] = useState('');
    const [IPs, setIPs] = useState([]);

    const handleNodeCount = (e) => {
        setNodeCount(e.target.value);
    }
    const handleIP = (e) => {
        setIP(e.target.value);
    }

    const handleAddIP = () => {
        setIPs([...IPs, IP]);
    }

    const handleEnter = (e) => {
        if (e.key === 'Enter') {
            handleAddIP();
        }
    }

    const handleDeleteIP = (deleteIP) => {
        return () => {
            setIPs(IPs.filter(ip => ip !== deleteIP));
        }
    }

    const updateSettings = () => {
        updateConfig().then(response => { });
    }

    useEffect(() => {
        setIPs([...IPs, ...servers.map(server => server.ipAddress)])
    }, [servers])

    return (
        <Card variant="outlined" className='card'>
            <Typography variant="h5" gutterBottom component="h2" className='card__title'>
                Настройки
            </Typography>
            <div className='settingsCard__inputs'>
                <FormControl fullWidth >
                    <TextField
                        id="nodesCount"
                        label="Количество необходимых узлов"
                        value={nodeCount}
                        onChange={handleNodeCount}
                        type="number"
                    />
                </FormControl>
                <div className='settingsCard__ipInput'>
                    <TextField
                        id="ipInput"
                        value={IP}
                        onChange={handleIP}
                        onKeyDown={handleEnter}
                        label="IP"
                        variant="outlined"
                    />
                    <Button variant="outlined" onClick={handleAddIP}><Icon>add_circle</Icon></Button>
                </div>
                <List dense={false}>
                    {IPs.map(ip => (
                        <ListItem
                            key={ip}
                            secondaryAction={
                                <IconButton edge="end" aria-label="delete" onClick={handleDeleteIP(ip)}>
                                    <Delete />
                                </IconButton>
                            }
                        >
                            <ListItemText
                                primary={ip}
                            />
                        </ListItem>
                    ))}
                </List>
                <Button variant="contained" onClick={updateSettings}>Применить</Button>
            </div>

        </Card>
    )
}

export default SettingsCard;