import { Card, Typography } from '@mui/material';
import React from 'react';

const declOfNum = (n, textForms) => {
    n = Math.abs(n) % 100;
    let n1 = n % 10;
    if (n > 10 && n < 20) { return textForms[2]; }
    if (n1 > 1 && n1 < 5) { return textForms[1]; }
    if (n1 === 1) { return textForms[0]; }
    return textForms[2];
}

const GlobalStatusCard = ({ workNodesCount, nodesCount, secretsCount }) => {

    return (
        <Card
            variant="outlined"
            className={`globalCard globalCard--${workNodesCount === nodesCount ?
                'green' : workNodesCount / nodesCount >= 0.7 ? 'yellow' : 'red'
                }`}
        >
            <div>
                <Typography variant="h3" className='globalCard__status'>
                    {`${workNodesCount}/${nodesCount}`} <span>активно</span>
                </Typography>

                <Typography variant="h5" className='globalCard__status'>
                    {secretsCount} <span>{declOfNum(secretsCount, ['секрет сохранен', 'секрета сохранено', 'секретов сохранено'])}</span>
                </Typography>
            </div>
        </Card>
    )
}

export default GlobalStatusCard;