import { useEffect, useState } from 'react';
import { Alert, Container } from '@mui/material';
import ServersCard from '../Components/ServersCard';
import Header from '../Components/Header';
import GlobalStatusCard from '../Components/GlobalStatusCard';
import SettingsCard from '../Components/SettingsCard';
import AsideMenu from '../Components/AsideMenu';
import { DefaultServerData, ServerData, TaskData } from '../MockData';
import { fetchServers } from '../http/serverAPI';
import { getGlobalStatus } from '../utils';

const Admin = () => {
    const [isError, setIsError] = useState(null);
    const [serverDataLoading, setServerDataLoading] = useState(false);
    const [serverData, setServerData] = useState(DefaultServerData);
    const [taskData, setTaskData] = useState(TaskData);


    useEffect(() => {
        setServerDataLoading(true);
        fetchServers().then((data) => {
            setServerDataLoading(false);
            setServerData(data);
        }).catch(err => {
            setServerDataLoading(false);
            setServerData(ServerData);
            setIsError(err);
        })
    }, []);

    return (
        <Container>
            {isError &&
                <Alert variant="outlined" severity="error">Error</Alert>
            }
            <div className='content'>
                <div className='content__col1'>

                    <SettingsCard servers={serverData.nodesStatuses} />
                    <ServersCard isLoading={serverDataLoading} servers={serverData.nodesStatuses} />

                </div>
                <div className='content__col2'>
                    <GlobalStatusCard
                        secretsCount={serverData.secretsCount}
                        workNodesCount={serverData.nodesStatuses.filter(node => getGlobalStatus(node) === 'Ok').length}
                        nodesCount={serverData.nodesStatuses.length}
                    />
                    {/* <TaskCard /> */}
                </div>
            </div>
        </Container>
    );
}

export default Admin;